<template>
    <el-container v-loading="loading">
        <div class="rise x add">
            <div class="title">{{title}}</div>
            <div class="content">
                <div class="item" v-for="(item, index) in data" :key="index">
                    <span>{{item.name}}</span>
                    <!-- input -->
                    <div v-if="!item.type">
                        <el-input v-if="item.module === 'name'" v-model="name" :placeholder="item.placeholder"/>
                        <el-input v-if="item.module === 'count'" v-model="count" :placeholder="item.placeholder"/>
                        <el-input v-if="item.module === 'password'" v-model="password" :placeholder="item.placeholder"/>
                        <el-input v-if="item.module === 'mobile'" v-model="mobile" :placeholder="item.placeholder"/>
                    </div>

                    <!-- select -->
                    <div v-if="item.type === 'select'">
                        <el-select v-if="item.module === 'role'" v-model="item.model" placeholder="全部">
                            <el-option
                                v-for="item in item.options"
                                :key="item.id"
                                :label="item.dValue"
                                :value="item.dValue"
                            ></el-option>
                        </el-select>
                    </div>
                </div>
            </div>
            <div class="btn-list">
                <el-button @click="cancle()" class="btn">取消</el-button>
                <el-button @click="save()" class="btn">保存</el-button>
            </div>
        </div>
    </el-container>
</template>

<script>
import queryString from "querystring";
import { DateDay } from 'utils/formData';
import { getRoles } from 'data/listBody';
const titles = ["新建", "编辑"];
const defaultV = [
    { name:'用户姓名：', placeholder:'请输入用户姓名', module: 'name'},
    { name:'用户账号：', placeholder:'请输入用户账号', module: 'count'},
    { name:'用户密码：', placeholder:'请输入用户密码', module:'password'},
    { name:'用户手机：', placeholder:'请输入用户手机', module:'mobile'},
    { name:'所属角色：', placeholder:'请选择所属角色', module:'role', type:'select'},
]

export default {
    name: "systemUseradd",
    data() {
        return {
            title: "",
            id: "",
            data: defaultV,            
            name:'',
            count:'',
            password:'',
            mobile:'',
            loading: false
        };
    },
    mounted() {
        this.dropInit();
        this.dataInit();
    },
    methods: {
        dropInit(){ // 数据初始化
            getRoles((res) => {
                let leftMenu = this.data.map(item => {
                    if(item.module === 'role') item.options = res;
                    return item;
                })
                this.data = leftMenu;
            })
        },
        dataInit() {
            const { type, id } = this.$route.query;
            if (type !== "edit") return (this.title = titles[0]);
            this.title = titles[1];
            this.id = id;
            this.ajax();
        },
        ajax() {
            //查看详情
            // this.loading = true;
            // const param = queryString.stringify(params || {});
            // let url = urls[code];
            // this.api.get(`/role`).then((res)=>{
            // this.loading = false;
            // this.data = res.data.list;
            // this.setPage(res.data)
            // this.dataInit();
            // }).catch((err) => {
            //   console.log(err)
            //   this.$message(err.message)
            // })
        },
        save() {
            //保存详情
        },
        cancle() {
            //取消
            this.$router.go(-1);
        }
    }
};
</script>
 
